import { createApp } from 'vue';
import App from "@comp/AutomaticLottery/index.vue";
import '@scss/AutomaticLottery/index.scss';
import mitt from "mitt";
import store from "@js/AutomaticLottery/store";
import router from '@js/AutomaticLottery/router';
import VueCookies from 'vue3-cookies';
import 'bootstrap-icons/font/bootstrap-icons.css';
import 'jquery/dist/jquery.js';
import '@fortawesome/fontawesome-free/css/all.css';

const eventBus = mitt();
const app = createApp(App);
app.config.globalProperties.emitter = eventBus;

app.use(VueCookies).use(store).use(router).mount("#app");
