<script>
import { mapActions, mapGetters } from 'vuex';
import { useRouter, useRoute } from 'vue-router';
import _ from 'lodash';
export default {
    name: "DocumentLibrary",
    components: {
        // PDFUpload,
        // DocumentList
    },
    data() {
        return {
            router: useRouter(),
            oute: useRoute(),
            newPlace: "", // 用於輸入新地點
            // places: [], // 地點清單
            result: null, // 抽籤結果
            url: '',//網址
            history: [], // 抽籤歷史
            blockList:[],
            DadDoesntWant: false,
            DoesntWantEatTooMuch: false,
            DoesntWantEatExpensive: false,
            lastResult: null // 上一次的抽籤結果
        }
    },
    computed: {
        ...mapGetters('lottery_data', [
            'getPlacesList',
            'getHistory',
            'getNoRepeat'
        ]),
        // 計算textarea高度
        places() {
            let filteredList = _.cloneDeep(this.getPlacesList);
            if (this.DadDoesntWant) {
                filteredList = filteredList.filter(place => !place.name.includes('貴族世家(中壢)'));
            }
            if (this.DoesntWantEatTooMuch) {
                filteredList = filteredList.filter(place => !place.name.includes('貴族世家'));
                filteredList = filteredList.filter(place => !place.name.includes('千葉火鍋'));
                filteredList = filteredList.filter(place => !place.name.includes('西堤牛排'));
            }
            if (this.DoesntWantEatExpensive) {
                filteredList = filteredList.filter(place => !place.name.includes('千葉火鍋(竹北)'));
                filteredList = filteredList.filter(place => !place.name.includes('西堤牛排'));
            }
            if (this.blockList.length > 0) {
                filteredList = filteredList.filter(place => !this.blockList.includes(place.name));
            }

            return filteredList
        },
    },
    watch: {
    },
    methods: {
        ...mapActions('lottery_data', [
            'updateNewPlace',
            'updateDeletePlace',
            'updateNoRepeat',
        ]),
        addPlace() {
            if (this.newPlace.trim() === "") {
                alert("請輸入有效地點！");
                return;
            }
            this.updateNewPlace(_.cloneDeep(this.newPlace.trim()));
        },
        // removePlace(index) {
        //     this.updateDeletePlace(index);
        // },
        blockPlace(placeName){
            this.blockList.push(_.cloneDeep(placeName));
            console.log(this.blockList);
            
        },
        drawRandomPlace() {
            if (this.places.length === 0) {
                alert("目前沒有任何地點可供抽籤！");
                return;
            }

            // 隨機選擇地點
            const randomIndex = Math.floor(Math.random() * this.places.length);
            this.result = this.places[randomIndex].name;
            this.url = this.places[randomIndex].url;

            // 記錄抽籤歷史
            const now = new Date();
            const time = `${now.getHours()}:${now.getMinutes()}:${now.getSeconds()}`;
            this.history.unshift({ place: this.result, time });

            // 保存上一次結果
            this.lastResult = this.result;
        },
        getHref() {
            if (this.url === '') {
                alert('無官方網站');
                return
            }
            window.open(`${this.url}`, '_blank');
        },
        resetList(){
            this.blockList = [];
            this.DadDoesntWant = false;
            this.DoesntWantEatTooMuch = false;
            this.DoesntWantEatExpensive = false;
        }
    },

    created() {
    },
    mounted() {
    }
};
</script>
<template>
    <section id="lottery_dinner" class="d-flex w-100 h-100">
        <section class="left_area p-3 d-flex flex-column w-50 flex-shrink-0">
            <div class="d-flex flex-column mb-2">
                <p>篩選條件：</p>
                <div class="d-flex" style="flex-wrap: wrap;">
                    <label class="d-flex justify-content-center align-items-center">
                        <input class="me-2" style="transform: scale(1.5);cursor: pointer;" type="checkbox"
                            v-model="DadDoesntWant" />
                        <span class="me-3" style="flex-shrink: 0;">老爸不想開車</span>
                    </label>
                    <label class="d-flex justify-content-center align-items-center">
                        <input class="me-2" style="transform: scale(1.5);cursor: pointer;" type="checkbox"
                            v-model="DoesntWantEatTooMuch" />
                        <span class="me-3" style="flex-shrink: 0;">不想吃太好</span>
                    </label>
                    <label class="d-flex justify-content-center align-items-center">
                        <input class="me-2" style="transform: scale(1.5);cursor: pointer;" type="checkbox"
                            v-model="DoesntWantEatExpensive" />
                        <span class="me-3" style="flex-shrink: 0;">不想吃很貴的</span>
                    </label>
                </div>
            </div>

            <div class="d-flex">
                <button class="btn btn-danger me-3" @click="drawRandomPlace" :disabled="places.length === 0">抽籤</button>
                <!-- <div>
                    <input class="h-100 me-3" type="text" v-model="newPlace" placeholder="請輸入地點" />
                    <button class="btn btn-success" @click="addPlace">添加地點</button>
                </div> -->
            </div>

            <div class="result h-100 pb-3 d-flex flex-column justify-content-center align-items-center" v-if="result">
                <h3 class="p-2" style="font-size: 24px;color: red; user-select: none;">✨抽籤結果✨</h3>
                <div style="text-align: center;">
                    <p class="result_name">{{ result }}</p>
                    <p class="result_url"  @click.prevent="getHref()">🔗點此開啟連結</p>
                </div>
            </div>
        </section>

        <section class="right_area w-50 h-100 d-flex flex-shrink-0">
            <div class="places_list w-50 h-100 flex-shrink-0" style="overflow: auto;">
                <h5 class="p-3 d-flex justify-content-between align-items-center">
                    <span>地點清單：</span>
                    <button class="btn btn-success" @click="resetList()">重置</button>
                </h5>
                <ul class="p-3" style="overflow: auto;">
                    <li class="mb-2 p-2 d-flex justify-content-between align-items-center"
                        v-for="(place, index) in places" :key="index">
                        <p>{{ place.name }}</p>
                        <!-- <button class="btn btn-danger" @click="removePlace(index)">刪除</button> -->
                        <button class="btn btn-danger" @click="blockPlace(place.name)">移除</button>
                    </li>
                </ul>
            </div>
            <div class="history_list w-50 h-100 flex-shrink-0" style="overflow: auto;border-left: 1px solid black;">
                <h5 class="p-3">歷史紀錄：</h5>
                <ul class="p-3" style="overflow: auto;">
                    <li v-for="record in history" :key="record.time"
                        class="mb-2 p-2  d-flex justify-content-between align-items-center">
                        {{ record.time }} - {{ record.place }}
                    </li>
                </ul>
            </div>
        </section>

    </section>
</template>